.App {
  font-family: "Open Sans", 'Lato'!important;
  text-align: center;

  margin-top: 10vh;
}

html, 
body {
    min-height: 100%;
    padding: 0;
    margin: 0;

    * {
      color: #FFF;
      font-size: 16px;
    }
}

h1  {
  font-size: 28px;
}

body { 
  // background-color: #509BF5;
  background-image: linear-gradient(#C074B2, #8AB5E8);
}

.data-table {
  max-width: 90vw;
  margin: 0 auto;

  .table-cell {
    text-align: left;
  }
}

button {
  color: #FFF;
  background-color: #1DB954;
  font-size: 16px;
  line-height: 1;
  border-radius: 500px;
  padding: 19px 56px 21px;  
  font-family: "Open Sans", 'Lato'!important;
  cursor: pointer;
  margin-top: 24px;
  margin-bottom: 12px;
}

.notification-message {  
  font-family: "Open Sans", 'Lato'!important;
}

.spotify-button {
  color: #FFF;
  background-color: #1DB954;
  font-size: 16px;
  line-height: 1;
  border-radius: 500px;
  padding: 19px 56px 21px;  
  font-family: "Open Sans", 'Lato'!important;
  cursor: pointer;
}